import React from 'react'
import propTypes from 'prop-types'
// import { connect } from 'react-redux'

import { Tag } from './components'

class TagList extends React.Component{

	constructor(props){
		super(props)
	}



	render(){
		return(
			<ul id="tags">
				<Tag />
			</ul>
		)
	}





}

TagList.propTypes = {
	tags : propTypes.array,
}



export default TagList



